  font-size: 12px;
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
}

.card__container {
  padding: 32px 16px 16px;
}

.card__mac-icon {
  max-width: 160px;
  min-height: 134px;
  margin: 0 auto;
  display: block;
}

.card__item {
  color: #060b35;
  margin-top: 40px;
  margin-bottom: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__product {
  color: #616070;
